import back from '@/components/back'
export default {
	data() {
		return {
			tabItem: null,
			active: null,
			info: null,
			drInfos: null,
			temp: false,
			flag: 0,
			DisGroupName: this.$route.query.DisGroupName,
			drMsg: this.$store.state.userInfo.Data.User,
			primaryDr: null, //基层医生
		};
	},
	components: {
		back
	},
	created() {
		let drInfoList = this.$store.state.ProDrInfos
		let disGroupMsg = this.$store.state.DisGroupMsg
		if(drInfoList && disGroupMsg && (Number(disGroupMsg.DisGroupId) == Number(this.$route.query.DisGroupId))) {
			this.drInfos = drInfoList
			this.flag = 6
			this.primaryDr = disGroupMsg.primaryDr
		} else {
			this.pullDr()
		}

	},
	mounted() {

	},
	methods: {
		pullDr() { //获取转诊医生的默认设置
			let primaryDr = null
			this.$get('PlatFormAPI/DoctorReferral/QueryDoctorReferralSet', {
				DisGroupId: this.$route.query.DisGroupId,
				DrId: this.drMsg.DoctorId,
			}).then(o => {
				this.info = o
				if(this.info) {
					let arr = [1, 2, 3, 4, 5]
					let infoArr = this.info.concat(arr.slice(this.info.length, 5))
					let level = null;
					this.drInfos = []
					this.drInfos.length = 5
					infoArr.forEach((v, idx) => {
						if(idx < (this.info.length)) {
							if(v.ProfessionalLevel == 1) {
								primaryDr = v.DrId
								this.primaryDr = v.DrId
							}
							level = v.ProfessionalLevel
							this.drInfos[level - 1] = v
						} else {
							level = v
							this.drInfos[level - 1] = {}
						}
						let n = ""
						switch(level) {
							case 1:
								n = '基层医生'
								break;
							case 2:
								n = '乡级医生'
								break;
							case 3:
								n = '区级医生'
								break;
							case 4:
								n = '市级医生'
								break;
							case 5:
								n = '省级医生'
								break;
							default:
								break;
						}
						this.drInfos[level - 1].ProfessionalName = n
						this.flag++
						/*if(!v.DrId && primaryDr) {
							let val = v
							this.$post('PlatFormAPI/DoctorReferral/QueryCountDoctorReferral', {
								DrId: primaryDr,
								ProfessionalLevels: val.ProfessionalLevel ? val.ProfessionalLevel : val,
								DisGroupId: this.$route.query.DisGroupId,
							}).then(o => {
								if(val.ProfessionalLevel) {
									this.drInfos[val.ProfessionalLevel - 1].drNum = o[0].Num.toString()
								} else {
									this.drInfos[val - 1].drNum = o[0].Num.toString()
									this.drInfos[val - 1].ProfessionalLevel = val
								}
								this.flag++
							})
						} else {
							this.flag++
						}*/
					})
				}
				this.temp = true
			})
		},
		goDrList(i) {
			if(i.ProfessionalLevel == 1){
				//基层医生不可设置
				return false;
			}
			this.$store.dispatch('pushProDrInfos', this.drInfos)
			this.$router.push({
				path: "/doctor-list",
				query: {
					id: this.primaryDr,
					level: i.ProfessionalLevel,
					DisGroupId: this.$route.query.DisGroupId,
					DisGroupName: this.$route.query.DisGroupName
				}
			})
		}
	}
};



